export default {
    addNum({ commit, state }, id){
        commit('REMBER_ANSWER', id);
        if (state.itemNum < state.itemDetail.length) {
            commit('ADD_ITEMNUM',1)
        }
    },
    initializeData({ commit }) {
        commit('INITIALIZE_DATA')
    },
  rememberTime({ commit }) {
    commit('REMBER_TIME')
  }
}



